@import url('https://fonts.googleapis.com/css2?family=Genos:wght@100&display=swap');

@media (max-width: 768px) {
    body {
        // background-image: url("./imgs/fundo.jpg");
        display: flex;
        flex-direction: column;
        align-items: center; /* centraliza verticalmente os elementos */
        justify-content: center; /* centraliza horizontalmente os elementos */
        margin: 0;
        padding: 0;
        font-family: 'Genos', serif;
        font-weight: bold;
        display: flex;
        color: aliceblue;
        user-select: none;
    }
    
    header {
        background-color: rgba(60,125,60,0.8);
        position: fixed;
        top: -2%;
        right: 0;
        width: 100%;
        height: 7%;
        display: flex;
        justify-content: space-evenly;
    }
    
    footer {
        background-color: rgba(60,125,60,0.8);
        position: fixed;
        right: 0;
        width: 100%;
        height: 8%;
        display: flex;
        justify-content: space-evenly;
        bottom: 0;
    }
    .devmir {
        background-color: rgba(250,250,250,0.8);
        display: flex;
        align-items: center;
        left: 0;
        top: 5%;
        position: fixed;
        height: 15%;
        width: 100%; 
        flex-direction: column;
    }

    #logo {
        height: 100%;
        align-self: center;
    }
    
    #about {
        background-color: rgba(60,125,60,0.8);
        width: 40%;
        height: 30%;
        border-radius: 40px;
        border: 0;
        font-family: 'Genos', serif;
        font-weight: bold;
        color: aliceblue;
        font-size: 150%;
    }
    
    #content {
        background-color: rgba(60,125,60,0.8);
        position: fixed;
        top: 27%;
        right: 0;
        width: 100%;
        height: 65%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }

    #complete_name {
        height: 2%;
    }
    
    #profile {
        width: 100%;
        height: 30%;
        text-align: center;
        height: 30%;
    }
    
    #icon {
        width: 30%;
        height: 80%;
        border-radius: 15px;
        transform: matrix(-1, 0, 0, 1, 0, 0);
        object-fit: cover;
    }
    
    #skills {
        right: 0;
        bottom: 0;
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        padding: 0;
        margin: 0;
    }
    
    #skills_title {
        position: relative;
        width: 100%;
        height: 40%;
        display: flex;
        flex-direction: column;
        align-items: center;   
    }
}

@media (min-width: 769px) {

body {
    // background-image: url("./imgs/fundo.jpg");
    margin: 0;
    padding: 0;
    font-family: 'Genos', serif;
    font-weight: bold;
    display: flex;
    color: aliceblue;
    user-select: none;
}

#logo {
    height: 32%;
    margin-top: 25%;
    margin-bottom: 20%;
}

header {
    background-color: rgba(60,125,60,0.8);
    position: fixed;
    top: 0;
    right: 0;
    width: 60%;
    height: 8%;
    display: flex;
    justify-content: space-evenly;
    text-align: justify;
}

footer {
    background-color: rgba(60,125,60,0.8);
    position: fixed;
    right: 0;
    width: 60%;
    height: 10%;
    display: flex;
    justify-content: space-evenly;
    bottom: 0;
}
.devmir {
    background-color: rgba(250,250,250,0.8);
    display: flex;
    align-items: center;
    position: fixed;
    height: 100%;
    width: 40%; 
    flex-direction: column;
}

#about {
    background-color: rgba(60,125,60,0.8);
    width: 40%;
    align-self: center;
    height: 5%;
    border-radius: 50px;
    border: 0;
    font-family: 'Genos', serif;
    font-weight: bold;
    color: aliceblue;
    font-size: 150%;
    margin-bottom: 10%;
}

#content {
    background-color: rgba(60,125,60,0.8);
    position: fixed;
    top: 8%;
    right: 0;
    width: 60%;
    height: 82%;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

#profile {
    width: 100%;
    height: 30%;
    text-align: center;
}

#icon {
    width: 25%;
    height: 80%;
    border-radius: 15px;
    transform: matrix(-1, 0, 0, 1, 0, 0);
    object-fit: cover;
}

#skills {
    right: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    padding: 0;
    margin: 0;
}

#skills_title {
    position: relative;
    width: 100%;
    height: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;   
}
}

.link {
    height: 5%;
    text-decoration: none;
    color: aliceblue;
    font-size: x-large;
    align-self: flex-start;
}

#skills_select {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

h2.off {
    opacity: 50%;
}
li.java { list-style: none;}

#skillButton {
    background: none;
    border: none;
    padding: 0;
    font-family: inherit;
    font-size:x-large;
    font-weight: 900;
    color: inherit;
  }

.mediaButton {
    background: none;
    border: none;
    width: 8%;
}